<template>
  <div class="header">
    <ul class="header-button-left">
      <li>Cancel</li>
    </ul>
    <ul class="header-button-right">
      <li v-if="step == 1" @click="step++">Next</li>
      <li v-if="step == 2" @click="publish">발행</li>
    </ul>
      <!--
    <h4>안녕 {{ $store.state.test }}</h4>
   <button @click="$store.commit('changeTest')">버튼</button>
    <button @click="changeTest">버튼</button>
    <button @click="$store.dispatch('more')">더보기버튼</button>
    <p>{{ $store.state.more }}</p>
    -->
    <img src="./assets/icon.png" class="logo" />
  </div>

  <Container
    :initData="initData"
    :step="step"
    :uploadImageUrl="uploadImageUrl"
    :class="filter"
  />
  <!--
    <p>{{name}} {{age}} {{likes}}</p>
    <p>{{anotherTest}}</p>

      <p>{{now()}} {{counter}}</p>
      <p>{{now2}} {{counter}}</p>
      <button @click="counter++">버튼</button>

  <button @click="more">더보기</button>
  -->
  <div class="footer">
    <ul class="footer-button-plus">
      <input
        @change="upload"
        type="file"
        id="file"
        class="inputfile"
        @write="inputText = $event"
      />
      <label for="file" class="input-plus">+</label>
    </ul>
  </div>

  <!--
    <div v-if="step == 0">내용0</div>
    <div v-if="step == 1">내용1</div>
    <div v-if="step == 2">내용2</div>
    <button @click="step = 0">버튼0</button>
    <button @click="step = 1">버튼1</button>
    <button @click="step = 2">버튼2</button>-->
</template>

<script>
import Container from "@/components/Container.vue";
import initData from "@/assets/data";
import axios from "axios";
import {mapMutations, mapState} from "vuex";

export default {
  name: "App",
  components: {
    Container,
  },
  data() {
    return {
      initData: [...initData],
      count: 0,
      step: 0,
      uploadImageUrl: "",
      inputText: "",
      selectedFilter: "",
        counter:0,
    };
  },
  computed: {
    // 처음 실행하고 값을 간직함
      // computed 함수는 return이 꼭 있어야함
    now2() {
      return new Date();
    },
      name() {
        return this.$store.state.test
      },
      ...mapState(['test', 'age', 'likes']),
      ...mapState({ anotherTest : 'test'}),
  },
  mounted() {
    this.emitter.on("addFilter", (a) => {
      console.log(a);
      this.selectedFilter = a;
    });
  },
  methods: {
      ...mapMutations(['chageLikes', 'setMore', 'changeTest']),

    // 사용할 떄마다 실행됨
    now() {
      return new Date();
    },
    publish() {
      var inputData = {
        name: "geonupark",
        userImage: "https://placeimg.com/100/100/people",
        postImage: this.uploadImageUrl,
        likes: 0,
        date: "May 15",
        liked: false,
        content: this.inputText,
        filter: this.selectedFilter,
      };
      this.initData.unshift(inputData);
      this.step = 0;
    },
    more() {
      axios
        .get(`https://codingapple1.github.io/vue/more${this.count++}.json`)
        .then((result) => {
          this.initData.push(result.data);
        });
    },
    upload(e) {
      let file = e.target.files;
      let url = URL.createObjectURL(file[0]);
      this.uploadImageUrl = url;
      this.step++;
    },
  },
};
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

body {
  margin: 0;
}
ul {
  padding: 5px;
  list-style-type: none;
}
.logo {
  width: 22px;
  margin: auto;
  display: block;
  position: absolute;
  left: 0;
  right: 0;
  top: 13px;
}
.header {
  width: 100%;
  height: 40px;
  background-color: white;
  padding-bottom: 8px;
  position: sticky;
  top: 0;
}
.header-button-left {
  color: skyblue;
  float: left;
  width: 50px;
  padding-left: 20px;
  cursor: pointer;
  margin-top: 10px;
}
.header-button-right {
  color: skyblue;
  float: right;
  width: 50px;
  cursor: pointer;
  margin-top: 10px;
}
.footer {
  width: 100%;
  position: sticky;
  bottom: 0;
  padding-bottom: 10px;
  background-color: white;
}
.footer-button-plus {
  width: 80px;
  margin: auto;
  text-align: center;
  cursor: pointer;
  font-size: 24px;
  padding-top: 12px;
}
.sample-box {
  width: 100%;
  height: 600px;
  background-color: bisque;
}
.inputfile {
  display: none;
}
.input-plus {
  cursor: pointer;
}
#app {
  box-sizing: border-box;
  font-family: "consolas";
  margin-top: 60px;
  width: 100%;
  max-width: 460px;
  margin: auto;
  position: relative;
  border-right: 1px solid #eee;
  border-left: 1px solid #eee;
}
</style>
