<template>
    <div style="padding : 10px" >
        <h4>팔로워</h4>
        <input placeholder="?" @input="search($event.target.value)"/>
        <div class="post-header" v-for="(a,i) in follower" :key="i">
            <div class="profile" :style="`background-image:url(${a.image})`"></div>
            <span class="profile-name">{{ a.name }}</span>
        </div>
    </div>
</template>

<script>
// eslint-disable-next-line no-unused-vars
import {computed, onMounted, ref, toRefs, watch} from "vue";
import axios from "axios";
// eslint-disable-next-line no-unused-vars
import {useStore} from "vuex";

export default {
    name : "MyPage",
    props :{
        one : Number
    },
    setup(){
        let follower = ref([]); // ref는 숫자, 문자같은 primitive data type을 담게 되어있지만 ref만 사용
        onMounted(()=>{
            axios.get('/follower.json').then((a)=>{
                follower.value = a.data
            })
        })

        function search(inputText){
            let newFollower = follower.value.filter((a)=>{
                return a.name.indexOf(inputText) != -1
            });
            follower.value = [...newFollower]
        }

      /*
        // reactive 사용법
        let test = reactive({name : 'kim'}) //reactive는 object, array 같은 reference data type을 주로 담고
        test;

        // props 사용법
        let { one } = toRefs(props)
        console.log(one.value)

        //watch 사용법
        watch( one.value, ()=>{ console.log(one.value) } )

        // computed 사용법
        let 어쩌구 = computed( ()=>{ return 10 } )
        console.log(어쩌구.value)

        //vuex store 사용법
        let store = useStore();
        console.log(store.state.test)
*/



        return { follower, search }
    },
}
</script>

