import { createStore } from 'vuex'
import axios from "axios";

// Create a new store instance.
const store = createStore({
    state () {
        return {
            count: 0,
            test : 'geonupark',
            age : 20,
            likes : 30,
            likesClick : false,
            more : {}
        }
    },
    mutations: {
        increment (state) {
            state.count++
        },
        changeTest(state){
            state.test = 'testPark'
        },
        chageLikes(state){
            if(!state.likesClick){
                state.likesClick = true
                state.likes++;
            } else {
                state.likesClick = false
                state.likes--;
            }
        },
        setMore(state,data){
            state.more = data
        }
    },
    actions : {
        // ajax나 오래걸리는 작업들하는 곳
        more(context) {
            axios
                .get(`https://codingapple1.github.io/vue/more0.json`)
                .then((result) => {
                    // state 변경시 무조건 mutaion 함수 만들어서 변경 할 것
                    context.commit('setMore', result.data);
                });
        },
    }
})
export default store