<template>
  <div
    :class="`${filter}`"
    class="filter-item"
    :style="`background-image:url(${uploadImageUrl})`"
    @click="addFilter"
  >
    <slot></slot>
  </div>
</template>
<script>

export default {
    // eslint-disable-next-line vue/multi-word-component-names
    name : 'FilterBox',
    props : {
        uploadImageUrl : String,
        filter : String,
    },
    methods : {
        addFilter() {
            this.emitter.emit('addFilter', this.filter)
        }
    }
}
</script>
<style>
.filter-item {
  width: 100px;
  height: 100px;
  margin: 10px 10px 10px auto;
  padding: 8px;
  display: inline-block;
  color: white;
  background-size: cover;
  background-position: center;
}
</style>
