<template>
  <div v-if="step === 0">
    <Post v-for="(a, i) in initData" :key="i" :rowData="a" />
  </div>
  <!-- 필터선택페이지 -->
  <div v-if="step === 1">
    <div
      class="upload-image"
      :style="`background-image:url(${uploadImageUrl})`"
      :class="selectedFilter"
    ></div>
    <div class="filters">
      <FilterBox :uploadImageUrl="uploadImageUrl" :filter="filter" v-for="filter in filters" :key="filter">
          {{filter}}
      </FilterBox >
    </div>
  </div>

  <!-- 글작성페이지 -->
  <div v-if="step === 2">
    <div
      class="upload-image"
      :style="`background-image:url(${uploadImageUrl})`"
      :class="selectedFilter"
    ></div>
    <div class="write">
      <textarea class="write-box" @input="$emit('write, $event.target.value')">
        write!
      </textarea>
    </div>
  </div>

    <div v-if="step === 3">
        <MyPage :one="2"></MyPage>
    </div>
</template>

<script>
import Post from "@/components/Post.vue";
import FilterBox from "@/components/FilterBox.vue";
import MyPage from "@/components/MyPage.vue";

export default {
  // eslint-disable-next-line vue/multi-word-component-names
  name: "Container",
  components: {MyPage, FilterBox, Post },
    data(){
      return {
          filters : [ "aden", "_1977", "brannan", "brooklyn", "clarendon", "earlybird", "gingham", "hudson",
              "inkwell", "kelvin", "lark", "lofi", "maven", "mayfair", "moon", "nashville", "perpetua",
              "reyes", "rise", "slumber", "stinson", "toaster", "valencia", "walden", "willow", "xpro2"],
          selectedFilter :''
      }
    },
    mounted() {
        this.emitter.on("addFilter", (a) => {
            this.selectedFilter = a;
        });
    },
  props: {
    initData: Array,
    step: Number,
    uploadImageUrl: String,

  },
};
</script>

<style>
.upload-image {
  width: 100%;
  height: 450px;
  background: cornflowerblue;
  background-size: cover;
}
.filters {
  overflow-x: scroll;
  white-space: nowrap;
}
.filter-1 {
  width: 100px;
  height: 100px;
  background-color: cornflowerblue;
  margin: 10px 10px 10px auto;
  padding: 8px;
  display: inline-block;
  color: white;
  background-size: cover;
}
.filters::-webkit-scrollbar {
  height: 5px;
}
.filters::-webkit-scrollbar-track {
  background: #f1f1f1;
}
.filters::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 5px;
}
.filters::-webkit-scrollbar-thumb:hover {
  background: #555;
}
.write-box {
  border: none;
  width: 90%;
  height: 100px;
  padding: 15px;
  margin: auto;
  display: block;
  outline: none;
}
</style>
